@import url('https://fonts.googleapis.com/css2?family=Orbitron&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap'); /* Adding Poppins font */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #121212;
  color: #fff;
  overflow-x: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  opacity: 0;
  transition: opacity 2s ease-in;
  position: relative;
  z-index: 1;
}

.App.visible {
  opacity: 1;
}

.navbar {
  background-color: #1e1e1e;
  color: white;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.nav-menu {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav-menu a,
.nav-menu button {
  color: white;
  text-decoration: none;
  transition: color 0.3s;
  background: none;
  border: none;
  cursor: pointer;
  font: inherit;
}

.nav-menu a:hover,
.nav-menu button:hover {
  color: #ff6666;
}

.main-content {
  flex: 1;
  padding: 2rem;
}

.hero {
  position: relative;
  text-align: center;
  padding: 4rem 2rem;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}

.background-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Enhanced Title to make "Triad Red" stand out more */
.title {
  font-family: 'Calisto MT', serif;
  font-size: 4rem;
  margin-bottom: 1rem;
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  color: #FF4C4C; /* Brighter Triad Red color */
  text-shadow: 3px 3px 8px rgba(0, 0, 0, 0.8); /* Stronger shadow for more depth */
  background: linear-gradient(45deg, #FF4C4C, #FF6666, #FF4C4C); /* Brighter gradient for a stunning effect */
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  animation: gradientAnimation 3s ease infinite;
  border: 2px solid #FF6666; /* Border for more prominence */
  border-radius: 10px; /* Increased border radius for a smoother look */
  box-shadow: 0 0 15px rgba(255, 0, 0, 0.7), 0 0 25px rgba(255, 0, 0, 0.5), 0 0 35px rgba(255, 0, 0, 0.3); /* Stronger glow effect */
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.subtitle {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.cta-button {
  background-color: #333;
  color: white;
  border: 2px solid #ff6666;
  padding: 0.8rem 2rem;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  border-radius: 5px;
}

.cta-button:hover {
  background-color: #444;
  transform: translateY(-2px);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.3);
}

/* Enhanced section-title for consistency */
.section-title {
  font-family: 'Calisto MT', serif;
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #FF4C4C; /* Brighter Triad Red color */
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3); /* Subtle shadow for depth */
}

.services {
  text-align: center;
  padding: 2rem 0;
}

.services-list {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

.service-item {
  background-color: #1e1e1e;
  padding: 1.5rem;
  border: 1px solid #444;
  border-radius: 8px;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column; /* Change to column for better mobile display */
  align-items: center; /* Center align items */
  text-align: center; /* Center align text */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s, background-color 0.3s, border-color 0.3s;
}

.service-item:hover {
  transform: translateY(-5px);
  background-color: #292929;
  border-color: #ff6666;
}

.service-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem; /* Adjusted margin for better spacing on mobile */
}

.service-content h3 {
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  color: #ff6666;
}

.service-content p {
  margin: 0;
  font-size: 1rem;
  color: #ccc;
}

.about {
  text-align: center;
  padding: 2rem 0;
}

.about-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
  line-height: 1.6;
}

/* Enhanced about-content h3 for consistency */
.about-content h3 {
  font-family: 'Calisto MT', serif;
  margin-top: 1.5rem;
  font-size: 1.5rem;
  color: #FF4C4C; /* Brighter Triad Red color */
  padding: 10px;
  border-radius: 8px;
  background-color: #292929;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.about-content p,
.about-content ul {
  margin-top: 1rem;
}

.about-content ul {
  list-style: disc;
  padding-left: 1.5rem;
}

.contact {
  text-align: center;
  padding: 2rem 0;
}

.contact-form {
  max-width: 600px;
  margin: 0 auto;
  background-color: #1e1e1e;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s;
}

.contact-form:hover {
  transform: translateY(-5px);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #444;
  border-radius: 5px;
  background-color: #121212;
  color: white;
  transition: border-color 0.3s;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #ff6666;
  outline: none;
}

.submit-button {
  background-color: #ff0000;
  color: white;
  border: none;
  padding: 0.8rem 2rem;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
}

.submit-button:hover {
  background-color: #cc0000;
  transform: translateY(-2px);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.3);
}

/* Enhanced logo-container for better display */
.logo-container {
  position: relative;
  display: inline-block;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.logo-container img {
  max-height: 80px;
  filter: drop-shadow(4px 4px 6px rgba(0, 0, 0, 0.5));
  transition: transform 0.3s, filter 0.3s, box-shadow 0.3s;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.3), 0 0 30px rgba(255, 255, 255, 0.2); /* Adding glow effect */
}

.logo-container img:hover {
  transform: translateY(-2px) scale(1.05);
  filter: drop-shadow(6px 6px 8px rgba(0, 0, 0, 0.7));
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.6), 0 0 30px rgba(255, 255, 255, 0.4), 0 0 40px rgba(255, 255, 255, 0.3); /* Stronger glow effect on hover */
}

.footer {
  background-color: #1e1e1e;
  color: white;
  text-align: center;
  padding: 1rem 0;
  font-size: 0.8rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .title {
    font-size: 2.5rem;
  }

  .subtitle {
    font-size: 1.2rem;
  }

  .cta-button {
    padding: 0.6rem 1.5rem;
    font-size: 0.9rem;
  }

  .section-title {
    font-size: 1.5rem;
  }

  .services-list {
    gap: 1rem;
  }

  .service-item {
    max-width: 100%;
  }

  .service-icon {
    font-size: 2rem;
  }

  .service-content h3 {
    font-size: 1.2rem;
  }

  .service-content p {
    font-size: 0.9rem;
  }

  .about-content {
    padding: 1rem;
  }

  .contact-form {
    padding: 1.5rem;
  }

  .form-group label {
    font-size: 0.9rem;
  }

  .form-group input,
  .form-group textarea {
    font-size: 0.9rem;
  }

  .submit-button {
    padding: 0.6rem 1.5rem;
    font-size: 0.9rem;
  }
}
